.app {
    display: grid;
    grid-template-rows: [header-start] 20vh [header-end container-start] 70vh [container-end footer-start] 10vh [footer-end];
    grid-template-columns: 1fr [content-start] minmax(min-content, 130rem) [content-end] 1fr;
}

.header,
.container,
.footer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header {
    grid-row: header-start / header-end;
    grid-column: content-start / content-end;

    font-family: 'Fira_Sans-medium', sans-serif;
    font-size: 5rem;
    font-weight: 600;
    letter-spacing: 0.25rem;
}

.container {
    grid-row: container-start / container-end;
    grid-column: content-start / content-end;

    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;

    border: 1px solid rgba(0, 0, 0, 0.25);
    font-size: 2rem;
}

.form-add-expense {
    padding: 2rem;
    height: 100%;
    border-right: 1px solid rgba(0, 0, 0, 0.25);
    display: grid;
    grid-template-rows: 1fr 5fr;

    &__title {
        display: flex;
        align-items: center;
        justify-content: center;

        font-size: 3rem;

        border-bottom: 1px solid rgba(0, 0, 0, 0.25);
    }

    &__form {
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        padding-top: 1rem;

        & > * {
            display: flex;
            flex-direction: column;
            position: relative;
            justify-content: center;
            gap: 1rem;
        }
    }
}

.list-of-expenses {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 2rem;

    &__total {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid rgba(0, 0, 0, 0.25);
        line-height: 5rem;
    }

    &__expenses {
        display: flex;
        flex-direction: column;
        margin: 2rem 0;
        justify-content: start;
        overflow-y: auto;
        height: 70%;

        & > * {
            display: grid;
            grid-template-columns: repeat(4, minmax(min-content, 1fr));

            &:first-child {
                border-bottom: 1px solid rgba(0, 0, 0, 0.25);
            }

            & > * {
                display: flex;
                padding: 1rem 2rem;
                justify-content: center;
                align-items: center;
            }
        }

        & button {
            background-color: white;
            border: 0;
            font-size: 2rem;
            transition: all 0.3s;

            &:hover {
                background-color: rgba(0, 0, 0, 0.05);
            }
            &:active {
                background-color: rgba(0, 0, 0, 0.1);
            }
        }
    }

    &__control {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        padding: 3.5rem 0;
    }
}

.footer {
    grid-row: footer-start / footer-end;
    grid-column: content-start / content-end;
    display: grid;
    grid-template-columns: repeat(2, minmax(min-content, 1fr));
    font-size: 1.5rem;

    & > * {
        display: flex;
    }

    &__copyright {
        & > span {
            margin-top: 1.5px;
            margin-right: 1rem;
        }
    }

    &__socials {
        justify-content: end;
        gap: 2rem;
    }
}

.middle-child {
    &__first {
        border-left: 1px solid rgba(0, 0, 0, 0.25);
        border-right: 1px solid rgba(0, 0, 0, 0.25);
    }
    &__second {
        border-right: 1px solid rgba(0, 0, 0, 0.25);
    }
}
