*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    box-sizing: inherit;
}

li {
    list-style: none;
}

a,
a:visited,
a:active,
a:hover {
    color: black;
    text-decoration: none;
    transition: all 0.2s;
}

a:hover {
    color: gray;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;
    font-family: 'Fira_Sans-regular', sans-serif;
}

@font-face {
    font-family: 'Fira_Sans-regular';
    src: url(../fonts/FiraSans-Regular.ttf);
}

@font-face {
    font-family: 'Fira_Sans-medium';
    src: url(../fonts/FiraSans-Medium.ttf);
}

@font-face {
    font-family: 'Acme';
    src: url(../fonts/Acme-Regular.ttf);
}
