input[type='button'] {
    padding: 0.5rem 2rem;
    background-color: whitesmoke;
    color: black;
    transition: all 0.2s;

    &:hover {
        background-color: rgba(0, 0, 0, 1);
        color: white;
    }
}

input {
    border: 1px solid rgba(0, 0, 0, 0.25);
    font-size: 2rem;
    padding: 0.5rem 2rem;
    border-radius: 0;
}

label {
    margin-left: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

select {
    width: 15%;
    font-size: 1.5rem;
    height: 100%;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, 0.25);
}

li:not(:first-child) {
    transition: all 0.1s;

    &:hover {
        background-color: rgba(0, 0, 0, 0.05);
    }
    &:active {
        background-color: rgba(0, 0, 0, 0.1);
    }
}

li.selectedExpense {
    background-color: rgba(0, 0, 0, 0.1);
}
